*{
  box-sizing: border-box;
}
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background: gray;
}
code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}
h2{
  background: rgb(219, 101, 120);
  padding: 18px;
  color: #fff;
  text-align: center;
}
.App{
  padding: 30px;
  background: #fff;
  width: 80%;
  margin: 100px auto;
  box-shadow: 1px 2px 5px #eee;
  border-radius: 10px;
}
input{
  width: 80%;
  border: 0;
  padding: 6px 10px;
  background: #e8e8e8;
}
button{
  width: 20%;
  border: 0;
  padding: 6px 0;
  margin: 0;
  outline: 0;
  color: #fff;
  font-weight: bold;
  cursor: pointer;
  background: #4dc55b;
}
ul{
  list-style: none;
  padding: 0;
  margin-top: 40px;
}
li{
  margin: 15px 0;
}
li span{
  display: inline-block;
  width: 60%;
  color: #6a6a6a;
  font-size: 20px;
  font-weight: 700;
}
li >button:first-of-type{
  background: #6a6a6a;
  color: #fff;
  margin-right: 1%;
  width: 19%;
}
li >button:first-of-type:hover{
  background: rgb(219, 101, 120);
  border-radius: 10px;
}
li >button:last-of-type{
  background: rgb(219, 101, 120);
  color: #fff;
}
li >button:last-of-type:hover{
  background: #6a6a6a;
  border-radius: 10px;
}
.App ul p{
  text-align: center;
  font-weight: 800;
  color: rgb(219, 101, 120);
}